// -----------------------------------------------------
// Description :
// provide all site icons
// -----------------------------------------------------
// Dependencies :
// -----------------------------------------------------

@mixin ico-async($ico-name) {
    background-image: svg("ico-#{$ico-name}");
}

%ico-check-mark {
    @include ico-async(check-mark);
}

%ico-key {
    @include ico-async(key);
}

%ico-premium-content {
    @include ico-async(premium-content);
}

%ico-time {
    @include ico-async(time);
}

.ca-ico {
    &--check-mark {
        @extend %ico-check-mark;
    }

    &--key {
        @extend %ico-key;
    }

    &--premium-content {
        @extend %ico-premium-content;
    }

    &--time {
        @extend %ico-time;
    }
}
